import React from "react";
import NationalLineChart from "./NationalLineChart/NationalLineChart";
import OverralStats from "./OverallStats/OverralStats";
import { covid } from "./data/covid_data";

import "./Dashboard.css";
import ProvincialTable from "./ProvincialTable/ProvincialTable";
import SouthAfricanMap from "./SouthAfricanMap/SouthAfricanMap";

export default function Dashboard() {
  const totalCovid = (value) =>
    covid.reduce((total, cases) => {
      return total + cases[value];
    }, 0);
  return (
    <section className="Dashboard">
      <div className="overview">
        <NationalLineChart covidData={covid} />
        <OverralStats covidData={covid} totalCovid={totalCovid} />
      </div>
      <div className="breakdown">
        <ProvincialTable covidData={covid} totalCovid={totalCovid} />
        <SouthAfricanMap />
      </div>
    </section>
  );
}
