import React from "react";
import OverrallDonutChart from "./OverrallDonutChart";

import "./OverralStats.css";

import barIcon from "./graph.svg";

export default function OverralStats({ covidData, totalCovid }) {
  return (
    <div className="Overrall-Stats">
      <div className="Card">
        <div>
          <OverrallDonutChart totalCovid={totalCovid} tested={168643} />
          <h3>
            <span>{((totalCovid("positive") / 168643) * 100).toFixed(2)}%</span>
            <span>Positive</span>
          </h3>
        </div>

        <footer>
          <img src={barIcon} alt="icon" />
          <h5>Tests Conducted</h5>
          <h4>168643</h4>
        </footer>
      </div>
      <div>
        <div className="Card">
          <img src={barIcon} alt="icon" />
          <h5>Positive Cases Identified</h5>
          <h4>{totalCovid("positive")}</h4>
        </div>
        <div className="Card">
          <img src={barIcon} alt="icon" />
          <h5>Recoveries</h5>
          <h4>{totalCovid("recoveries")}</h4>
        </div>
        <div className="Card">
          <img src={barIcon} alt="icon" />
          <h5>Deaths</h5>
          <h4>{totalCovid("deaths")}</h4>
        </div>
      </div>
    </div>
  );
}
