import React, { useEffect, useState } from "react";
import * as d3 from "d3";
import moment from "moment";

export default function NationalLineChartGraph({ covidData }) {
  const [selectProvince, setselectProvince] = useState(null);

  const results = selectProvince
    ? covidData.filter((covid) =>
        covid.province
          .toLowerCase()
          .includes(selectProvince.toLocaleLowerCase())
      )
    : covidData;

  const startDate = (value) =>
    moment().subtract(value, "days").format("DD/MM/YYYY");

  const dailyCases = (date, province) =>
    results.map((covid) =>
      covid.daily_cases.filter((daily) => {
        return (daily.date === date || covid.province === province) && daily;
      })
    );

  const totalDailyCases = (day) => dailyCases(day).map((covid) => covid[0]);
  const dayOne = totalDailyCases(startDate(7), selectProvince).reduce(
    (total, day) => {
      const checkDay = day ? day.number : 0;
      return total + checkDay;
    },
    0
  );
  const dayTwo = totalDailyCases(startDate(6), selectProvince).reduce(
    (total, day) => {
      const checkDay = day ? day.number : 0;
      return total + checkDay;
    },
    0
  );
  const dayThree = totalDailyCases(startDate(5), selectProvince).reduce(
    (total, day) => {
      const checkDay = day ? day.number : 0;
      return total + checkDay;
    },
    0
  );
  const dayFour = totalDailyCases(startDate(4), selectProvince).reduce(
    (total, day) => {
      const checkDay = day ? day.number : 0;
      return total + checkDay;
    },
    0
  );
  const dayFive = totalDailyCases(startDate(3), selectProvince).reduce(
    (total, day) => {
      const checkDay = day ? day.number : 0;
      return total + checkDay;
    },
    0
  );
  const daySix = totalDailyCases(startDate(2), selectProvince).reduce(
    (total, day) => {
      const checkDay = day ? day.number : 0;
      return total + checkDay;
    },
    0
  );
  const daySeven = totalDailyCases(startDate(1), selectProvince).reduce(
    (total, day) => {
      const checkDay = day ? day.number : 0;
      return total + checkDay;
    },
    0
  );

  const total =
    dayOne + dayTwo + dayThree + dayFour + dayFive + daySix + daySeven;

  const days = (value) =>
    moment().subtract(value, "days").format("DD-MMM-YYYY");

  const data = [
    {
      date: days(14),
      SA: 0,
    },
    {
      date: days(13),
      SA: 0,
    },
    {
      date: days(12),
      SA: 0,
    },
    {
      date: days(11),
      SA: 0,
    },
    {
      date: days(10),
      SA: 0,
    },
    {
      date: days(9),
      SA: 0,
    },
    {
      date: days(8),
      SA: 0,
    },
    {
      date: days(7),
      SA: daySeven,
    },
    {
      date: days(6),
      SA: daySix,
    },
    {
      date: days(5),
      SA: dayFive,
    },
    {
      date: days(4),
      SA: dayFour,
    },
    {
      date: days(3),
      SA: dayThree,
    },
    {
      date: days(2),
      SA: dayTwo,
    },
    {
      date: days(1),
      SA: dayOne,
    },
  ];

  useEffect(() => {
    // set the dimensions and margins of the graph
    const width = 780;
    const height = 320;
    const margin = 10;
    const padding = 5;
    const adj = 30;
    // we are appending SVG first
    const svg = d3
      .select("#linechart")
      .append("svg")
      .attr("preserveAspectRatio", "xMinYMin meet")
      .attr(
        "viewBox",
        "-" +
          adj +
          " -" +
          adj +
          " " +
          (width + adj * 3) +
          " " +
          (height + adj * 3)
      )
      .style("padding", padding)
      .style("margin", margin)
      .classed("svg-content", true);

    let columns = ["date", "SA"];

    const timeConv = d3.timeParse("%d-%b-%Y");
    const dataset = data;
    // dataset.then(function (data) {
    const slices = columns.slice(1).map(function (id) {
      return {
        id: id,
        values: data.map(function (d) {
          return {
            date: timeConv(d.date),
            measurement: +d[id],
          };
        }),
      };
    });
    // console.log("Column headers", columns);
    // console.log("Column headers without date", columns.slice(1));
    // // returns the sliced dataset
    // console.log("Slices", slices);
    // // returns the first slice
    // console.log("First slice", slices[0]);
    // // returns the array in the first slice
    // console.log("A array", slices[0].values);
    // // returns the date of the first row in the first slice
    // console.log("Date element", slices[0].values[0].date);
    // // returns the array's length
    // console.log("Array length", slices[0].values.length);

    const xScale = d3.scaleTime().range([0, width]);
    const yScale = d3.scaleLinear().rangeRound([height, 0]);
    xScale.domain(
      d3.extent(data, function (d) {
        return timeConv(d.date);
      })
    );
    yScale.domain([
      0,
      d3.max(slices, function (c) {
        return d3.max(c.values, function (d) {
          return d.measurement + 4;
        });
      }),
    ]);

    const yaxis = d3.axisLeft().ticks(slices[0].values.length).scale(yScale);

    const xaxis = d3
      .axisBottom()
      .ticks(d3.timeDay.every(1))
      .tickFormat(d3.timeFormat("%b %d"))
      .scale(xScale);

    svg
      .append("g")
      .attr("class", "axis")
      .attr("transform", "translate(0," + height + ")")
      .call(xaxis);

    svg.append("g").attr("class", "axis").call(yaxis);

    //this you had
    svg
      .append("g")
      .attr("class", "axis")
      .call(yaxis)
      //this you append
      .append("text")
      .attr("transform", "rotate(-90)")
      .attr("dy", ".75em")
      .attr("y", 6)
      .style("text-anchor", "end")
      .text("Frequency");

    const line = d3
      .line()
      .x(function (d) {
        return xScale(d.date);
      })
      .y(function (d) {
        return yScale(d.measurement);
      });

    // const lines = svg.selectAll("lines").data(slices).enter().append("g");

    // lines.append("path").attr("d", function (d) {
    //   return line(d.values);
    // });

    let id = 0;
    const ids = function () {
      return "line-" + id++;
    };

    const lines = svg.selectAll("lines").data(slices).enter().append("g");

    lines
      .append("path")
      .attr("class", ids)
      .attr("d", function (d) {
        return line(d.values);
      });

    lines
      .append("text")
      .attr("class", "serie_label")
      .datum(function (d) {
        return {
          id: d.id,
          value: d.values[d.values.length - 1],
        };
      })
      .attr("transform", function (d) {
        return (
          "translate(" +
          (xScale(d.value.date) + 10) +
          "," +
          (yScale(d.value.measurement) + 5) +
          ")"
        );
      })
      .attr("x", 5)
      .text(function (d) {
        // return "Serie " + d.id;
        return d.id;
      });
  }, [data]);

  return <div id="linechart" className="linechart"></div>;
}
