import React, { useState } from "react";
import NationalLineChartGraph from "./NationalLineChartGraph";
import ProvincialLineChartGraph from "./ProvincialLineChartGraph";

import "./NationalLineChart.css";

export default function NationalLineChart({ covidData }) {
  const [toggleLineCharts, setToggleLineCharts] = useState(false);

  return (
    <>
      <header>
        <h2>Monthly Stats</h2>
        <select>
          <option onClick={() => setToggleLineCharts(false)}>National</option>
          <option onClick={() => setToggleLineCharts(!false)}>
            Provincial
          </option>
        </select>
      </header>
      <div className="National-LineChart">
        {!toggleLineCharts ? (
          <NationalLineChartGraph covidData={covidData} />
        ) : (
          <ProvincialLineChartGraph covidData={covidData} />
        )}
      </div>
    </>
  );
}
