import React from "react";

import "./ProvincialTable.css";

export default function ProvincialTable({ covidData, totalCovid }) {
  return (
    <div className="Provincial-Table">
      <ul>
        <li>Province</li>
        <li>Positive</li>
        <li>Recoveries</li>
        <li>Deaths</li>
        <li>%</li>
      </ul>

      <div>
        {covidData.map((covid, i) => {
          return (
            <ul key={i}>
              <li>{covid.province}</li>
              <li>{covid.positive}</li>
              <li>{covid.recoveries}</li>
              <li>{covid.deaths}</li>
              <li>
                {((covid.positive / totalCovid("positive")) * 100).toFixed(2)}%
              </li>
            </ul>
          );
        })}
      </div>
      <ul>
        <li>Grand Total</li>
        <li>{totalCovid("positive")}</li>
        <li>{totalCovid("recoveries")}</li>
        <li>{totalCovid("deaths")}</li>
        <li>100%</li>
      </ul>
    </div>
  );
}
