import React, { useEffect } from "react";
import * as d3 from "d3";

export default function OverrallDonutChart({ totalCovid, tested }) {
  const getData = [
    {
      leave: "TestS Conducted",
      value: tested,
    },
    {
      total: "Positive",
      value: totalCovid("positive"),
    },
  ];

  useEffect(() => {
    let width = 500;
    let height = Math.min(width, 320);
    const radius = Math.min(width, height) / 2;

    let pie = d3
      .pie()
      .padAngle(0.005)
      .sort(null)
      .value((d) => d.value);

    let arc = d3
      .arc()
      .innerRadius(280 * 0.5)
      .outerRadius(radius - 1);

    let color = d3
      .scaleOrdinal()
      .domain(getData.map((d) => d.leave))
      .range(["rgba(255, 255, 255, .15)", "#fff"]);

    const arcs = pie(getData);

    const svg = d3
      .select("#donutchart")
      .append("svg")
      .attr("viewBox", [-width / 3, -height / 2, width, height]);

    svg
      .selectAll("path")
      .data(arcs)
      .join("path")
      .attr("fill", (d) => color(d.data.leave))
      .attr("d", arc)
      .append("title")
      .text((d) => `${d.data.leave}: ${d.data.value.toLocaleString()}`);

    svg
      .append("g")
      .attr("font-family", "sans-serif")
      .attr("font-size", 28)
      .attr("text-anchor", "middle")
      .selectAll("text")
      .data(arcs)
      .join("text")
      .attr("transform", (d) => `translate(${arc.centroid(d)})`);
    // .call((text) =>
    //   text
    //     .append("tspan")
    //     .attr("y", "-0.4em")
    //     .attr("x", "-5.2em")
    //     .attr("font-size", "1.25rem")
    //     .attr("font-weight", "bold")
    //     .text((d) => d.data.total)
    // );
    // .call(text =>
    //   text
    //     .filter(d => d.endAngle - d.startAngle > 0.25)
    //     .append("tspan")
    //     .attr("x", 0)
    //     .attr("y", "0.7em")
    //     .attr("fill-opacity", 0.7)
    //     .text(d => d.data.value.toLocaleString())
    // );
  }, [getData]);

  return <div id="donutchart" className="donutchart"></div>;
}
